import Vue from "vue";
import VueRouter from "vue-router";
import AppRegister from "@/views/Register";
import AppLogin from "@/views/Login";
import AppHome from "@/views/Home";
import AppEditor from "@/views/Editor";
import AppSettings from "@/views/Settings";
import AppArticle from "@/views/Article";
import AppUser from "@/views/User";

Vue.use(VueRouter);

const routes = [
  {
    path: "/realworld-frontend/",
    name: "home",
    component: AppHome
  },
  {
    path: "/realworld-frontend/register",
    name: "register",
    component: AppRegister
  },
  {
    path: "/realworld-frontend/login",
    name: "login",
    component: AppLogin
  },
  {
    path: "/realworld-frontend/editor/:slug",
    name: "editor_slug",
    component: AppEditor
  },
  {
    path: "/realworld-frontend/editor",
    name: "editor",
    component: AppEditor
  },
  {
    path: "/realworld-frontend/settings",
    name: "settings",
    component: AppSettings
  },
  {
    path: "/realworld-frontend/@:username",
    name: "user",
    component: AppUser
  },
  {
    path: "/realworld-frontend/article/:slug",
    name: "article",
    component: AppArticle
  }
];

export default new VueRouter({
  mode: "history",
  routes
});
